* {
  margin: 0;
  padding: 0;
  /* font-family: sans-serif; */
}


html {
  scroll-behavior: smooth;
}

#navbar {
  display: flex;
  align-items: center;
  position: sticky;
  top: 0px;
  height: 60px;
}

#navbar ul {
  /* font-family: 'Baloo Bhai', cursive; */
  /* font-family: 'Baloo Bhai 2', cursive; */
  font-family: 'Inter', sans-serif;
  display: flex;
}

#navbar::before {
  content: "";
  background-color: black;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  z-index: -1;
  opacity:1;
}

#navbar ul li {
  list-style: none;
  font-size: 1.3rem;
}

#navbar ul li a {
  color: white;
  display: block;
  padding: 3px 22px;
  border-radius: 20px;
  text-decoration: none;
}

#navbar ul li a:hover {
  color: black;
  background-color: white;
}

.split {
  float: right;
  position: absolute;
  right: 0;
  /* top: -15px; */
}

.link-1 {
  right: 230px;
  position: absolute;
}

.link-3 {
  right: 20px;
  position: absolute;
}
/* Home Section */
#home1{
  flex-direction: column;
  padding: 10px 20px;
  height: 80px;
  justify-content: center;
  align-items: center;
}

#home1::before {

  position: absolute;
  top: 0px;
  left: 0px;
  height: 665px;
  width: 100%;
  z-index: -1;
  opacity: 0.9;
}

#home1 h2 {
  color: #be2828;
  text-align: center;
}

#home1 p {
  color: white;
  text-align: center;
  font-size: 1.5rem;
}


.content {
  width: 100%;
  position: absolute;
  top: 6%;
  transform: translateY(-505);
  text-align: center;
  color: #fff;
}

.content h1 {
  font-size: 70px;
  margin-top: 80px;
  color: rgb(252, 51, 16);
}

.content p {
  margin: 20px auto;
  font-weight: 100;
  line-height: 25;
}

.drop input {
  height: 35px;
  width: 200px;
}

.drop select {
  height: 35px;
}

/* Services section */

#services {
  margin: 35px;
  display: flex;
  flex-direction: row;
  justify-content: center;

}

#services .hbox {
  border: 2px solid brown;
  padding: 10px 20px;
  margin: 25px 10px;
  background-color: #f3eaea;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
}

#services .hbox img {
  height: 150px;
  display: block;
  margin: auto;
}

#services .hbox p {
  font-family: 'Bree Serif', serif;
}


/* Contact Section */
#contact {
  position: relative;
}

#contact::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: url(../images/contactus.webp) no-repeat center center/cover; */
  z-index: -1;
  opacity: 0.5;
}

#contact-hbox {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 35px;
}

#contact-hbox input,
#contact-hbox textarea {
  width: 100%;
  padding: 0.5rem;
  border-radius: 10px;
  font-size: 1.2rem;
}

#contact-hbox form {
  width: 40%;
}

#contact-hbox label {
  font-size: 1.2rem;
  font-family: 'Bree Serif', serif;
}

button {
  width: 200px;
  padding: 10px 0;
  margin: 20px 10px;
  border-radius: 25px;
  font-weight: bold;
  border: 2px solid #ffffff;
  background: transparent;
  color: #ffffff;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  height: 55px;
}

