/* Utility classes */
.h-primary {
  font-family: 'Bree Serif', serif;
  font-size: 3.5rem;
  padding: 10px;
}

.h-secondary {
  font-family: 'Bree Serif', serif;
  font-size: 2.1rem;
  padding: 10px;
}



.btn {
  /* padding: 5px 20px;
  margin: 15px; */
  width: 100%;
  color: white;
  border: 2px solid black;
  background-color: rgb(119 23 23);
  font-size: 1.2rem;
  border-radius: 10px;
  cursor: pointer;
}

.center {
  text-align: center;
}

footer {
  background: black;
  color: white;
  padding: 5px 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer .col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

footer h4 {
  font-size: 14px;
  padding-bottom: 20px;
}

footer p {
  font-size: 13px;
  margin: 0 0 8px 0;
}

footer a {
  font-size: 13px;
  text-decoration: none;
  color: white;
  margin: 0 0 8px 0;
}

footer .follow {
  margin-top: 20px;
}

footer .follow i {
  /* color: #465b52; */
  padding-right: 4px;
  cursor: pointer;
}

footer .install .row img {
  border: 1px solid #088178;
  border-radius: 6px;
}

footer .install img {
  margin: 10px 0 15px 0;
}

footer .follow i:hover,
footer a:hover {
  color: #008178
}

footer .copyright {
  width: 100%;
  text-align: center;
}
